:root {
 font-size: 1rem;
--BGColor: "#C5DB94";
--TitleColor: "#443427";
--SubtitleColor: "#68894D";
--TextColor: "#4d453f";
--LinkColor: var(--SubtitleColor);
--ALinkColor: "#f2ffd6";
--VLinkColor: var(--TitleColor);
--ShadowColor: "#2e251e80";
}

body::before {
  content: "";
  position: fixed;
  top: 0; left: 0;
  width: 100%; height: 100%;
  background-color: #242224;
  z-index: -1;
}

body {
  margin: 0;
  color: #d1dede;
  font-size: 1.15rem;
  font-weight: bold;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
a {
  text-decoration: none;
  color: #b3c6a4;
}
a:visited {
  color:#94aa71;
}
a:active {
  color: #f2ffd6;
}
a:hover {
  color: #f2ffd6;
}
h1 {
  color: #0d1111;
}
h2, h3 {
  color: #b3c6a4;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}